
.root{
	font-size:15px;
	/* background-color: #f7f8fa; */
}
.show{
	display: block !important;
}
.header{
	position: fixed;
	top:0px;
	width:100%;
    background: #fff;
    border-bottom: 1px solid rgba(30,35,42,.06);
    box-shadow: 0 1px 3px 0 rgba(0,34,77,.05);
    background-clip: content-box;
    color:#8590a6;
    z-index:100;
    background-color: #20a0ff;
}
.headerInner{
	position: relative;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	width:1280px;
	height:50px;
	margin:0 auto;
	padding:0 20px;
	color:#8590a6;
}
.head-title{
	font-weight: bold;
	width:130px;
	margin-right:132px;
	color:#fff;
	font-size: 21px;
}
.head-nav{
}
.head-nav-item{
	padding:15px;
	/* color:#8590a6; */
	color:#fff;
}
.current-menu{
	background: #1d8ce0;
}
.new-info{
	width:16px;
	height: 16px;
	color:white;
	margin-right:20px;
	cursor: pointer;
}
.user-info{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	margin-left: 300px;
	position: relative;
}
.user-image{
	width: 30px;
	height:30px;
	cursor: pointer;
}
.user-name{
	width: 80px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin: 0 20px;
    color: white;
    height: 50px;
    line-height: 50px;
    padding: 0 20px;
}
.user-name:hover{
	background: #1d8ce0;
}
.user-name-hover{
	background: #1d8ce0;
}
.drop-down{
	display: none;
	position: absolute;
	top:50px;
	left:87px;
}
.drop-down li{
	display: block;
    width: 80px;
    height: 40px;
    line-height: 40px;
    padding:0 20px;
    color: #fff;
    text-decoration: none;
    border-top: 1px solid #58b7ff;
    box-shadow: 0 1px 1px rgba(0,0,0,.4);
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    cursor: pointer;
    background-color:#1d8ce0;
}
.drop-down li a{
	color: #ffffff;
}
.drop-down li:hover{
	background: #20a0ff;
}
.main{
	box-sizing: border-box;
	min-height: 650px;
	width: 1280px;
	margin:50px auto 10px;
	padding-top: 20px;
	overflow:hidden;
	background: #fff;
}
.footer{
	width: 100%;
	height:10px;
	margin-bottom:-20px;
	display: block;
}



