
@import "../../assets/css/common.css";
.user-logo{
  margin:30px;
  float:left;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #20a0ff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.info-detail{
  float:left;
  width:700px;
}
.first{
  width:80px;
  height:30px;
  line-height: 30px;
  text-align: right;
  margin:15px;
  text-overflow: ellipsis;
  overflow:hidden;
  white-space: nowrap;
}
.next{
  width:200px;
  height:30px;
  line-height: 30px;
  text-align: left;
  margin:15px;
  text-overflow: ellipsis;
  overflow:hidden;
  white-space: nowrap;
}



