
.show-video{
	margin:20px 10px 0 0;
	float:left;
	width:49%;
	height:550px;
}
.rating-table{
	float:left;
	width:49%;
	margin-top:20px;
	border:1px solid #cccccc;
	height: 550px;
	overflow-y: scroll;
}
.rating-info-top{
	
	border-bottom:1px solid #cccccc;
	padding: 10px;
}
.rating-info-top>span{
	width:40%;
}
.rating-info-detail{
	min-height: 300px
}
.question-item{
	margin:10px 0;
	padding:0 10px;
}
.question-item-title{
	font-size: 18px;
	font-weight: bold;
}
.question-item-detail{
	display: inline-block;
	width:70%;
	margin:7px 0;
}
.question-item-grade{
	display: inline-block;

	float:right;
}



