
@import "../../assets/css/common.css";
.left-menu{
      float:left;
      width:150px;
      height:600px;
      border: 1px solid #e1eaf1;
      box-shadow: 0 1px 3px 0 rgba(0,34,77,.05);
      background-clip: content-box;
      position:relative;
}
.left-menu>ul>li{
      display:block;
      width:100%;
      height:50px;
      text-align:center;
      color:#8590a6;
      margin:0 auto;
      line-height:50px;
      border-bottom:1px solid #e1eaf1;
}
.left-menu>ul>li:hover{
      background-color:#eef1f6;
      color:#20a0ff;
      cursor:pointer;
}
.li-focus{
      background-color:#20a0ff  !important;
      color:white !important;
}
.storage-left{
      position:absolute;
      width:100%;
      height:70px;
      bottom:0px;
      padding:20px 2px;
      box-sizing:border-box;
}
.right-container{
      float:left;
      width:calc(100% - 180px);
      min-height:600px;
      border: 1px solid #e1eaf1;
      box-shadow: 0 1px 3px 0 rgba(0,34,77,.05);
      background-clip: content-box;
      margin-left:20px;
      padding:10px;
      box-sizing:border-box;
}
.tools{
      display:-webkit-box;
      display:-ms-flexbox;
      display:flex;
      -webkit-box-pack:justify;
          -ms-flex-pack:justify;
              justify-content:space-between;
}
.file-list{
      margin:20px 20px 0;
      padding:10px 10px 5px;
      display:-webkit-box;
      display:-ms-flexbox;
      display:flex;
      -ms-flex-wrap:wrap;
          flex-wrap:wrap;
      overflow-y:auto;
      height:450px;
}
.file-item{
      position:relative;
      width:118px;
      height:140px;
      box-sizing:border-box;
      margin-right:10px;
      margin-bottom:10px;
      padding:15px 15px 0;
      box-sizing:border-box;
      border:1px solid #ffffff;
      font-size:13px;
      border-radius:5px;
}
.file-item:hover{
      background:#e1eaf1;
      border:1px solid #20a0ff;
}
.file-item-hover{
      background:#e1eaf1;
      border:1px solid #20a0ff;
}
.file-rename{
      position:absolute;
      width:90%;
      margin:0 5px;
      bottom:10px;
      left:1px;
}
.check{
      position:absolute;
      top:0px;
      right:0px;
      width:20px;
      height:20px;

      box-sizing:border-box;
      opacity:0;
}
.download-logo{
      position:absolute;
      top:0px;
      left:0px;
      width:30px;
      height:30px;
      box-sizing:border-box;
      opacity:0;
      cursor:pointer;
}
.file-item:hover .check{
      opacity:1;
      background-image:url(../../assets/img/nochecked.png);
      background-size:100% 100%;
}
.file-item:hover .download-logo{
      opacity:1;
      background-image:url(../../assets/img/down.png);
      background-size:100% 100%;
}
.folderNone{
      display: none !important;
}
.checked{
      position:absolute;
      top:0px;
      right:0px;
      width:20px;
      height:20px;
      background-size:100% 100%;
      box-sizing:border-box;
      opacity:1;
      background-image:url(../../assets/img/checked.png);
}
.file-item:nth-of-type(8n){
      margin-right:0px
}
.file-img{
      width:80px;
      height:80px;
}
.file-name{
      overflow : hidden;
      text-overflow: ellipsis;
      width:90px;
      white-space:nowrap;
}
.file-name-input{
      position:absolute;
      width:90%;
      margin:0 5px;
      bottom:10px;
      left:1px;
      height:20px;
      border-radius:5px;
}
.choose-stu{
      width:300px;
      margin:15px auto;
}
.downloadImg{
      display: block;
      width: 100%;
      height: 100%;
}



